  background-repeat: repeat-y;
  height: 265px;
  width: 137px;
  position: absolute;
  z-index: 0;
}

.participate:before {
  left: 0;
  top: 135px;
}

.participate:after {
  right: 0;
  bottom: -170px;
}

.participate__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

