  width: 137px;
  bottom: 410px;
  left: 0;
}

.participants__section {
  position: relative;
}

.participants__section:after {
  content: '';
  display: block;
  background-image: url(../img/sprite.svg#dots-grey);
  background-repeat: repeat-y;
  height: 180px;
  width: 174px;
  position: absolute;
  left: 91%;
